/* Search */
.search-wrap {
  background-color: white;
  color: #0a1832;
  z-index: 9997;
  position: absolute;
  top: 0;
  display: flex;
  width: calc(100% - 240px);
  right: 0;
  height: 70px;
  padding: 0 15px;
  transform: translate3d(0, -100%, 0);
  transition: 0.3s;
}
.search-wrap form {
  display: flex;
  width: 100%;
}
.search-wrap .search-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.search-wrap .search-input {
  flex: 1 1;
  border: none;
  outline: none;
  box-shadow: none;
  background-color: transparent;
}
.search-wrap .close-search {
  width: 36px;
  height: 64px;
  line-height: 64px;
  text-align: center;
  color: inherit;
  font-size: 24px;
}
.search-wrap .close-search:hover {
  color: #2f8ee0;
}

.search-wrap.open {
  transform: translate3d(0, 0, 0);
}

.megamenu-list li {
  position: relative;
  padding: 5px 0px;
}
.megamenu-list li a {
